import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isMenu: true,
    isContact: false,
    isContactView: false,
    mobile: true
  },
  getters: {
    getIsMenu: state => {
      return state.isMenu;
    },
    getIsContact: state => {
      return state.isContact;
    },
    getIsContactView: state => {
      return state.isContactView;
    },
    getIsMobile: state => {
      return state.mobile;
    }
  },
  mutations: {
    handleResize: state => {
      if (window.innerWidth < 992) {
        state.mobile = true;
      }
      if (window.innerWidth > 992) {
        state.mobile = false;
      }
    },
    toggleIsMenu: state => {
      if (state.isContact === true) {
        state.isContact = false;
      }
      state.isMenu = true;
    },
    toggleIsContact: state => {
      if (state.isMenu === true) {
        state.isMenu = false;
      }
      state.isContact = true;
    },
    toggleIsContactView: state => {
      state.isContactView = !state.isContactView;
    },
    hideAll: state => {
      state.isContact = false;
      state.isMenu = false;
    }
  },
  actions: {
    handleResize: context => {
      context.commit("handleResize");
    },
    toggleIsMenu: context => {
      context.commit("toggleIsMenu");
    },
    toggleIsContact: context => {
      context.commit("toggleIsContact");
    },
    toggleIsContactView: context => {
      context.commit("toggleIsContactView");
    },
    hideAll: context => {
      context.commit("hideAll");
    }
  }
});
